@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

.content-recovery {
  .title {
    @include background("../images/recovery/title_recovery.jpg", 750px, 381px);

    margin: 0 auto;
    margin-bottom: pxToRem(10px);
  }
  .sec-qa {
    padding: pxToRem(68px) pxToRem(50px) pxToRem(86px);
    h2 {
      padding-bottom: pxToRem(18px);

      font-size: pxToRem(30px);

      color: #ffbf00;
      &:before {
        padding-right: pxToRem(18px);

        font-size: pxToRem(50px);

        content: attr(data-no);
      }
    }
    p {
      padding-bottom: pxToRem(60px);

      font-size: pxToRem(26px);
      line-height: pxToRem(44px);

      color: #333;
    }
  }
  .sec-desc {
    padding: 0 pxToRem(50px) pxToRem(148px);
    h4 {
      margin: 0 auto pxToRem(44px);
    }
    .title1 {
      @include background("../images/recovery/title1.png", 423px, 30px);
    }
    .title2 {
      @include background("../images/recovery/title2.png", 214px, 28px);
    }
    p {
      padding-bottom: pxToRem(94px);

      font-size: pxToRem(26px);
      line-height: pxToRem(44px);

      color: #333;
    }
    .pattern {
      @include background("../images/recovery/pattern.png", 650px, 410px);

      margin: 0 auto;
    }
  }
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;

  box-sizing: border-box;
  padding: pxToRem(30px);

  background: #fff;
}

