.pop-city {
  display: none;

  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  .header {
    height: pxToRem(98px);

    font-size: pxToRem(32px);
    line-height: pxToRem(98px);
    text-align: center;
    a {
      display: inline-block;

      padding: 0 pxToRem(30px);
    }
    h4 {
      display: inline-block;
    }
    .cancel {
      float: left;

      color: #999;
    }
    .sure {
      float: right;

      color: #ffbf00;
    }
  }
  .tip {
    position: relative;

    padding-left: pxToRem(76px);
    height: pxToRem(64px);

    font-size: pxToRem(24px);
    line-height: pxToRem(64px);
    text-align: left;

    color: #ff6a4d;
    background-color: #fff0ed;
    &:before {
      position: absolute;
      top: 50%;
      left: pxToRem(30px);

      width: pxToRem(28px);
      height: pxToRem(28px);

      background: url("data:img/jpg;base64,") no-repeat center;
      background-size: contain;

      transform: translate(0, -50%);

      content: "";
    }
  }
  .list-wrapper {
    // position: absolute;
    // top: pxToRem(98px + 64px);
    // right: 0;
    // bottom: 0;
    // left: 0;
    // overflow: scroll;
  }
  .list-city {
    overflow: scroll;

    position: absolute;
    top: pxToRem(98px + 64px);
    right: 0;
    bottom: 0;
    left: 0;

    padding-left: pxToRem(30px);
    li {
      position: relative;

      height: pxToRem(98px);

      font-size: pxToRem(32px);
      line-height: pxToRem(98px);

      color: #333;
      &:after {
        @include border(bottom, #e5e5e5);
      }
      &.selected {
        &:before {
          @include background("data:img/jpg;base64,", 32px, 26px);

          position: absolute;
          top: 50%;
          right: pxToRem(30px);

          transform: translate(0, -50%);

          content: "";
        }
      }
    }
  }
}
